import React from "react";
import { Link, Box, Flex, Text, Button, Stack} from "@chakra-ui/react";

import Logo from "./Logo";

const NavBar = (props) => {
  const [isOpen, setIsOpen] = React.useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (
    <NavBarContainer {...props}>
      <Logo
        w="100px"
        color={["white", "white", "primary.500", "primary.500"]}
      />
      <MenuToggle toggle={toggle} isOpen={isOpen} />
      <MenuLinks isOpen={isOpen} />
    </NavBarContainer>
  );
};

const CloseIcon = () => (
  <svg width="24" viewBox="0 0 18 18" xmlns="http://www.w3.org/2000/svg">
    <title>Close</title>
    <path
      fill="#2D3748"
      d="M9.00023 7.58599L13.9502 2.63599L15.3642 4.04999L10.4142 8.99999L15.3642 13.95L13.9502 15.364L9.00023 10.414L4.05023 15.364L2.63623 13.95L7.58623 8.99999L2.63623 4.04999L4.05023 2.63599L9.00023 7.58599Z"
    />
  </svg>
);

const MenuIcon = () => (
  <svg
    width="24px"
    viewBox="0 0 20 20"
    xmlns="http://www.w3.org/2000/svg"
    fill="#2D3748"
  >
    <title>Menu</title>
    <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" />
  </svg>
);

const MenuToggle = ({ toggle, isOpen }) => {
  return (
    <Box display={{ base: "block", md: "none" }} onClick={toggle}>
      {isOpen ? <CloseIcon /> : <MenuIcon />}
    </Box>
  );
};


const MenuLinks = ({ isOpen }) => {
    const mystyle = {
        textDecoration: 'none'
    }
  return (
    <Box
      display={{ base: isOpen ? "block" : "none", md: "block" }}
      flexBasis={{ base: "100%", md: "auto" }}
    >
      <Stack
        spacing={8}
        align="left"
        justify={["center", "space-between", "flex-end", "flex-end"]}
        direction={["column", "row", "row", "row"]}
        pt={[10, 4, 0, 0]}
        fontSize="lg"
        color="gray.700"
      >
        <Box display={{ base: isOpen ? "block" : "none", md: "block", lg: "none"}}>
            <Stack direction="row">
                <Link style={mystyle}>Personal</Link>
                <Text>.</Text>
                <Link style={mystyle} color="gray.500">Business</Link>
            </Stack>    
        </Box>  
        <Link style={mystyle} href="#" >Zenefits</Link>
        <Link style={mystyle} href="#">Features</Link>
        <Link style={mystyle} href="#">Pricing</Link>
        <Link style={mystyle} href="#" >
          <Button
            mt="-10px"
            size="lg"
            borderRadius="30px"
            borderColor="gray.700"
            border="2px"
            color="gray.700"
            bg={["white", "white", "primary.500", "primary.500"]}
            _hover={{
              bg: "gray.300"
            }}
          >
            Get app
          </Button>
        </Link>
      </Stack>
    </Box>
  );
};

const NavBarContainer = ({ children, ...props }) => {
  return (
    <Flex
      as="nav"
      align="center"
      justify="space-between"
      wrap="wrap"
      w="100%"
      p={6}
      bg={["primary.500", "primary.500", "transparent", "transparent"]}
      color="black"
      {...props}
    >
      {children}
    </Flex>
  );
};

export default NavBar;